@import "variables";
.topbanner-bg-image {
  width: 100%;
  height: 100vh;
}

.casestudy-main-banner-section {
  padding: 90px 0 130px;

  .container {
    .casestudy-banner-wrapper {
      justify-content: center;
      gap: 30px;
      margin-bottom: 75px;
      display: flex;

      .banner-content {
        width: 50%;
        padding: 0 5px;
        @media (max-width: $small-tablet) {
          width: 100%;
          padding: 0 25px;
        }

        .text-wrapper {
          text-align: center;

          h4 {
            text-transform: uppercase;
            font-family: atten_newmedium, sans-serif;
            font-size: 35px;
            letter-spacing: 0;
            line-height: 41px;;
            color: $black;
            @media (max-width: $small-tablet) {
              font-size: 24px;
              line-height: 40px;
            }
          }

          h3 {
            font-family: 'Yeseva One Regular';
            font-size: 70px;
            line-height: 81px;
            color: $white;
            position: relative;
            @media (max-width: $small-tablet) {
              font-size: 42px;
              line-height: 60px;
            }

            &:after {
              position: absolute;
              left: 0;
              width: 35px;
              height: 2px;
              content: "";
              right: 0px;
              margin: auto;
              background-color: $black;
              bottom: -100px;

            }
          }
        }
      }
    }
  }
}

.logo-tabs {
  padding: 40px 0px;
}


.wrap {
 height: 100%;
    text-align: center;
    width: 100%;
    padding: 1em 2.5em;
}
.logo-tb {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	align-content: center;
	justify-content: center;
	align-items: center;
        flex-wrap: wrap;
}


.logo-tb li {
       padding-right: 25px;
    list-style: none;
    padding-left: 25px;
}
.logo-tb li:last-child {
    padding-right: 0;
}
.tool {
	cursor: pointer;
	position: relative;
        display: inline-block;
	&::before {
		top: 100%;
		content: url("../assets/images/icon-arrow-up-black.svg");
        width: 70px;
        height: 70px;
		    margin-left: -2.5em;
		transition: all .65s cubic-bezier(.84,-0.18,.31,1.26), opacity .65s .5s;
		transform: scale(.6) translateY(-90%);
	}
	&::after {
	color: #edeff0;
    content: attr(data-tip);
    width: 600px;
    min-height: 172px;
    background: #000;
    margin-left: -8.75em;
    padding: 40px;
    transition: all .65s cubic-bezier(.84, -.18, .31, 1.26) .2s;
    top: 100px;
    transform: scale(.6)translateY(50%);
    font-size: 34px;
    font-weight: 300;
    letter-spacing: 0;
    line-height: 40px;
    text-align: left;
	}
}

.tool {
	&::before {
		left: 50%;
		opacity: 0;
		position: absolute;
		z-index: -100;
	}
	&::after {
		left: 50%;
		opacity: 0;
		position: absolute;
		z-index: -100;
	}
}


.tool {
	&:hover {
		&::before {
			opacity: 1;
			transform: scale(1) translateY(0);
			z-index: 100;
		}
		&::after {
			opacity: 1;
			transform: scale(1) translateY(0);
			z-index: 100;
		}
	}
	&:focus {
		&::before {
			opacity: 1;
			transform: scale(1) translateY(0);
			z-index: 100;
		}
		&::after {
			opacity: 1;
			transform: scale(1) translateY(0);
			z-index: 100;
		}
	}
}


.tool {
	&:hover {
		&::before {
			transition: all .65s cubic-bezier(.84,-0.18,.31,1.26) .2s;
		}
	}
	&:focus {
		&::before {
			transition: all .65s cubic-bezier(.84,-0.18,.31,1.26) .2s;
		}
	}
}


.tool {
	&:hover {
		&::after {
			transition: all .65s cubic-bezier(.84,-0.18,.31,1.26);
		}
	}
	&:focus {
		&::after {
			transition: all .65s cubic-bezier(.84,-0.18,.31,1.26);
		}
	}
}








.casestudy-text {
        margin-bottom: 75px;

    .container{


        .text-wrapper
        {
            width: 50%;
    padding: 0 5px;
            margin: 0 auto;
          @media (max-width: $small-tablet) {
        width: 100%;
    padding: 0 25px;
      }
    p {

        font-size: 19px;
  letter-spacing: 0;
  line-height: 29px;
              padding-bottom: 20px;

            }

        }


    }


}



