@import "variables";
.topbanner-bg-image {
  background-image: url("../../src/assets/images/media-banner-image.jpg");
  width: 100%;
  height: 100vh;
  background-size: cover !important;
  background-repeat: no-repeat !important;
  background-position: center center !important;
}

.media-banner-section {
  padding: 90px 0 190px 0;

  .media-banner-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    margin-bottom: 75px;

    .media-banner-content {
      flex-direction: column;
      align-items: center;
      display: flex;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      .mediabanner-logo {
        width: 55px;
      }

      h3 {
        font-family: "Permanent Marker";
        font-size: 70px;
        line-height: 102px;
        color: $white;
         @media (max-width: $small-tablet) {
           font-size: 42px;
      }   
      }
    }
  }
}

.media-creative-section {
  padding: 106px 100px 108px 100px;
  background-color: $white;
  position: relative;
  @media (max-width: $small-tablet) {
       padding: 106px 25px 108px 25px;
      }
  .media-creative-wrapper {
  flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 70px;
    display: flex;
    width: 60%;
    margin: 0 auto;
    position: relative;  
      @media (max-width: $large-tablet){
       width: 100%;
      }
       @media (max-width: $small-tablet){
         flex-direction: column;
      }  
    .media-creative-content-left,
    .media-creative-content-right {
      p {
        color: $black-80;
        font-family: "atten_newbook", sans-serif;
        font-size: 19px;
        padding-bottom: 35px;
        letter-spacing: 0;
        line-height: 29px;
      }
    }

    .find-more {
      position: absolute;
      height: 104px;
      width: 357px;
      background-color: $bule;
      right: 0px;
      bottom: -120px;
       @media (max-width: $small-tablet) {
       width: 100%;
    margin: 0 auto;
    position: relative;
    bottom: 0;
    right: 0;
      } 
      a {
        color: #ffffff;
        font-family: "atten_newregular", sans-serif;
        font-size: 22px;
        font-weight: 500;
        letter-spacing: 0;
        line-height: 29px;
        display: block;
        width: 100%;
        height: 100%;
        padding: 25px;
                 @media (max-width: $small-tablet) {
   
        font-size: 18px;
          } 
          
        img {
          position: absolute;
          right: -32px;
          bottom: 15px;
          width: 60px;
        @media (max-width: $small-tablet) {
             width: 34px;
    bottom: 15px;
    right: 15px;
          }    
        }
      }
    }
  }
}

.recruiting-tax-section {
    padding: 106px 100px 0;
  overflow: hidden;
 background: rgb(255,194,0);
background: -moz-linear-gradient(0deg, rgba(255,194,0,1) 0%, rgba(255,194,0,1) 25%, rgba(0,0,0,1) 25%, rgba(0,0,0,1) 100%);
background: -webkit-linear-gradient(0deg, rgba(255,194,0,1) 0%, rgba(255,194,0,1) 25%, rgba(0,0,0,1) 25%, rgba(0,0,0,1) 100%);
background: linear-gradient(0deg, rgba(255,194,0,1) 0%, rgba(255,194,0,1) 25%, rgba(0,0,0,1) 25%, rgba(0,0,0,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffc200",endColorstr="#000000",GradientType=1);
    &.casestudy-yellow-bg
    {
         background: #ffc200;   
    }
    @media (max-width: $small-tablet) {
             padding: 50px 25px 0;
      }     
.title-wrapper {
       width: 100%;
    padding: 0px 133px;
    @media (max-width: $large-tablet) {
     padding: 0px;
      }
  h4 {
    color: $white;
    font-family: "atten_newbook", sans-serif;
    font-size: 34px;
    font-weight: 300;
    letter-spacing: 0;
    line-height: 40px;
    margin-bottom: 45px;
    max-width: 450px;
  }
}
.outer-wrapper {
    background-color: $white;
      padding: 75px;
    width: 60%;
    margin: 0 auto;
@media (max-width: $large-tablet) {
          width: 100%;
     padding: 50px  25px;
      }    
    
.logo-wrapper {
  /*     width: 175px;*/
    height: 40px;
    margin-bottom: 30px;
}     
  .recruiting-tax-wrapper {
    display: flex;
    gap: 50px;
@media (max-width: $small-tablet) {
        flex-direction: column;
      }

    .recruiting-tax-content {
      width: 50%;
@media (max-width: $small-tablet) {
          width: 100%;
      }
      p {
        color: $black-80;
        font-family: "atten_newregular", sans-serif;
        font-size: 18px;
        font-weight: 300;
        letter-spacing: 0;
        line-height: 28px;
        margin-bottom: 30px;
        label {
          font-family: atten_newmedium, sans-serif;
          font-style: italic;

          span {
            display: block;
            padding-bottom: 25px;
            font-style: normal;  
            b {
              font-weight: 900;
            }
          }
        }
      }
    }
  }
}
  .next-bg {
       width: 60%;
    height: 60vh;
    padding: 30px;
    position: relative;
    background-position: center !important;
    background-repeat: no-repeat !important;
    background-size: cover !important;
    margin: 0 auto;
@media (max-width: $large-tablet) {
          width: 100%;
      }
    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(
        0deg,
        $black -100%,
        rgba(255, 255, 255, 0) 100%
      );
    }

    &.image-next {
      background-image: url("../../src/assets/images/next-image.jpg");
    }

    .button-text-wrapper {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: flex;
      flex-direction: column;
      align-items: center;

      p {
        color: $white;
        font-family: "atten_newbook", sans-serif;
        font-size: 41px;
        font-weight: 300;
        letter-spacing: -1px;
        line-height: 48px;
        text-align: center;
        padding-bottom: 50px;
  @media (max-width: $large-tablet) {
             font-size: 24px;
    padding-bottom: 20px;
    line-height: 25px;
      }      
      }
    }
  }
}

@keyframes beat {
  to {
    transform: scale(1.3);
  }
}
