@import "variables";


.contact-wrapper {
  padding: 50px 25px;
  height: calc(100vh - 130px);
  overflow: hidden;
  .contact-content {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    transition: all 1.5s linear;
    &.moveLeft{
      left: -40%;
      transition: all 0.5s linear;
    }
    &.moveRight{
      right: -40%;
      transition: all 0.5s linear;
    }
    .map-wrapper {
      width: 30%;
      text-align: center;
      .map-box{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 61.7%;
        transition: all 1.5s linear;
        &.leftMap {
          left: -100%;
          &.show{
            left: -25px;
            transition: all 0.5s linear;
          }
        }
        &.rightMap {
          right: -100%;
          &.show{
            right: -25px;
            transition: all 0.5s linear;
          }
          .map-inner{
            flex-direction: row-reverse;
          }
        }
        .map-inner{
          height: 466px;
          background-color: $white;
          display: flex;
          justify-content: center;
          align-items: stretch;
          width: 100%;
          .map{
            width: 50%;
            iframe {
              width: 100%;
              height: 100%;
            }
          }
          .address-wrapper{
            width: 50%;
            padding: 75px 77px;
            .address-content {
              flex-direction: column;
              justify-content: flex-start;
              align-items: flex-start;
              margin: 0 auto;
              display: flex;
              position: relative;
              text-align: left;
              @media (max-width: $large-phone) {
                max-width: 100%;
              }
              label {
                margin-bottom: 10px;
                opacity: 0.6;
                color: $black;
                font-size: 16px;
                font-family: 'atten_newmedium', sans-serif;
                letter-spacing: 0.1px;
                line-height: 23px;
              }
              a {
                margin-bottom: 30px;
                color: $black;
                font-size: 22px;
                font-family: 'atten_newmedium', sans-serif;
                letter-spacing: 0.1px;
                line-height: 23px;
              }
              p {
                margin-bottom: 20px;
                color: $black;
                font-size: 18px;
                font-family: 'atten_newregular', sans-serif;
                letter-spacing: 0.1px;
                line-height: 27px;
              }
            }
          }
        }
      }
    }
    .content-wrapper {
      width: 40%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 32px;
      .text-wrapper {
        text-align: center;
        max-width: 350px;
        p {
          color: $black;
          font-family: 'atten_newregular', sans-serif;
          font-size: 34px;
          font-weight: 300;
          letter-spacing: 0;
          line-height: 40px;
          text-align: center;
        }
      }
      .image-wrapper {
        width: 30%;
        display: flex;
        justify-content: center;
        align-items: center;
        #hideRightMap, #hideLeftMap {
          background-color: transparent;
          border: none;
          outline: none;
          img{
            width: 45px;
            &.left-arrow{
              transform: rotate(180deg);
            }
          }
        }
      }
    }
  }
}
